            <span>简介：</span>
            <p>
              {{projectInfo.description}}
            </p>
          </div>
          <div class="summary">
            <span>项目背景材料：</span>
            <p>
              项目背景<br>
              {{projectInfo.background}}
            </p>
            <p>
              项目目标<br>
              {{projectInfo.target}}
            </p>
          </div>
          <!--<div class="summary">-->
            <!--<span>团队：</span><span class="tip"><i class="iconfont icongantanhao"></i>当团队开启项目执行任务后，其他人员将无法加入团队。</span>-->
            <!--<div class="member-list">-->
              <!--<div class="box member-box" v-for="obj in projectInfo.members" v-bind:key="obj.userCode">-->
                <!--<div class="name">{{obj.userName}}</div>-->
                <!--<div class="position">{{obj.groupLeader === 1 ? '队长' : '队员'}}</div>-->
<!--&lt;!&ndash;                <div class="step">初级菜鸟</div>&ndash;&gt;-->
                <!--<div class="head-portrait"></div>-->
              <!--</div>-->
            <!--</div>-->
          <!--</div>-->
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Header from '../../../components/school-training/Header.vue'
import ProjectTitle from '../../../components/school-training/ProjectTitle.vue'
export default {
  name: 'app',
  components: {
    Header,
    ProjectTitle
  },
  mounted () {
    this.initEvent()
    this.initData()
  },
  data () {
    return {
      winWidth: window.innerWidth,
      teamId: '',
      projectInfo: {},
      baseInfo: {},
      taskData: [{
        id: 1,
        name: '任务前测评',
        time: '2018.12.19',
        children: [{
          id: 1,
          name: '功能需求理解',
          content: '针对给出的业务功能模块功能特性，理解模块的功能需求和非功能需求（质量属性和设计约束）。'
        }]
      }, {
        id: 2,
        name: '项目策划',
        time: '2018.12.19',
        children: [{
          id: 1,
          name: '功能需求理解',
          content: '针对给出的业务功能模块功能特性，理解模块的功能需求和非功能需求（质量属性和设计约束）。'
        }]
      }, {
        id: 3,
        name: '需求分析',
        time: '2018.12.19',
        children: [{
          id: 1,
          name: '功能需求理解',
          content: '针对给出的业务功能模块功能特性，理解模块的功能需求和非功能需求（质量属性和设计约束）。'
        }]
      }]
    }
  },
  methods: {
    initEvent: function () {
      let me = this
      me.winWidth = $('body').width()
      window.onresize = function () {
        me.winWidth = $('body').width()
      }
    },
    initData: function () {
      this.loadProjectinfo()
    },
    loadProjectinfo () {
      let me = this
      // 获取团队id
      this.$http.post('/teambuild/queryteaminfoforleader', {}, res => {
        if (res.success) {
          me.teamId = res.data.teamId
          localStorage.setItem('teamId', res.data.teamId)
          me.$http.post('/doproject/projectinfo', {
            'teamid': me.teamId
          }, res => {
            if (res.success) {
              me.projectInfo = res.data
              let projectId = localStorage.getItem('projectId')
              let courseId = localStorage.getItem('courseId')
              me.$http.post('/teambuild/queryprojectinfo', {
                projectId: projectId,
                courseId: courseId
              }, res => {
                if (res.success) {
                  me.baseInfo = res.data
                  me.baseInfo.courseName = localStorage.getItem('courseName')
                }
              })
            }
          })
        }
      })
    },
    beginProject () {
      let me = this
      this.$http.post('/teambuild/startproject', {
        'teamId': me.teamId
      }, res => {
        if (res.success) {
          window.location = 'task-list.html'
        } else {
          // 返回错误的处理
        }
      })
      // window.location = 'task-execute.html'
    },
    goTeam () {
      window.location = 'team-search.html'
    }
  }
}
</script>

